@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600&family=Poppins:wght@700&display=swap);
*{margin:0;padding:0;border:0}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}button,a{cursor:pointer}body{margin:0;box-sizing:border-box;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1{font-family:"Poppins", sans-serif;font-size:40px}label,button,p,li,ol,input,textarea{font-family:"Open Sans", sans-serif}p,textarea,input{font-size:14px}ol{list-style:inside decimal;padding-left:0}main.page-home{min-height:100vh}@media screen and (min-width: 1024px){main.page-home{display:grid;grid-template-columns:1fr 1fr}}.initial,.generate-link{display:flex;align-items:center;justify-content:center}.initial{background-color:#075e54;color:#fff;padding:0 32px}@media screen and (max-width: 1024px){.initial{min-height:100vh}}@media screen and (max-width: 1024px){.generate-link{padding:64px 0}}.page-name ol li{margin-top:16px}.page-name h1{margin-bottom:24px}.form-container{max-width:450px;width:100%;height:480px;padding:60px 40px;box-sizing:border-box;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.2);border-radius:10px}@media screen and (max-width: 1024px){.form-container{margin:0 16px}}.copy-link-form{display:block}.generate-link-form{display:none}label{font-weight:600;display:block;margin-bottom:16px}.PhoneInputInput,textarea,.display-link{background:#fafafa;width:100%;border-radius:10px;outline:none;padding:12px;min-height:32px;box-sizing:border-box;font-size:14px;border:2px solid transparent;transition:all 0.5s ease-out}.PhoneInputInput:hover,.PhoneInputInput:active,.PhoneInputInput:focus,textarea:hover,textarea:active,textarea:focus,.display-link:hover,.display-link:active,.display-link:focus{border:2px solid #25d366}.error .PhoneInputInput{border:2px solid #cc0000 !important}textarea{resize:none;height:120px}.input-container{margin-bottom:32px}.display-link{display:flex;justify-content:space-between;max-height:56px}.display-link input{width:90%;outline:none;background-color:transparent}button.btn{width:100%;padding:12px 16px;height:50px;background-color:#128c7e;outline:none;border-radius:10px;position:relative;font-weight:600;font-size:16px;color:#fff}.icon-button{background-color:#075e54;position:absolute;left:0;top:0;height:100%;width:50px;display:flex;align-items:center;justify-content:center;border-top-left-radius:10px;border-bottom-left-radius:10px}button.btn-copy{background-color:transparent;outline:none;color:#25d366}

